<template>
    <main>
        <!--  -->
        <header class="header" :style="{opacity:$store.state.menuOpacity}" @keyup.enter="search" v-show="searchVis">
            <el-menu :default-active="activeIndex" class="menu" mode="horizontal" @select="goRouter">
                <el-menu-item @click="menuClick" v-show="menuOpacity>=0.4" class="item" index="/home">Home</el-menu-item>
                <el-menu-item @click="menuClick" v-show="menuOpacity>=0.4" class="item" index="/blog">Blog</el-menu-item>
                <!-- <el-menu-item v-show="menuOpacity>=0.4" class="item" index="/demo">Demo</el-menu-item> -->
                <el-autocomplete
                        v-show="menuOpacity>=0.4"
                        class="search"
                        placeholder="search for ..."
                        v-model="key"
                        icon="search"
                        @select="handleSelect"
                        :trigger-on-focus="false"
                        :fetch-suggestions="querySearch"
                        :on-icon-click="search">
                </el-autocomplete>
                <i class="el-icon-search mobile-search" @click="searchInMobile"></i>
                <!--<side-slipping></side-slipping>-->
            </el-menu>   
        </header>
        <!--  -->
        <main class="container">
            <router-view></router-view>
        </main>
        <!--  -->
        <footer class="footer"  v-show="searchVis">
            <div class="footer-inner">
                <span></span>
                <span class="tech" v-for="item in tech">{{item.name}}<span class="edition">{{'@'+item.edition}}</span></span>
            </div>
        </footer>
        <!--  -->
        <go-top></go-top>
        <!--  -->
        <!--<my-search :searchShow="searchVis"></my-search>-->
    </main>
</template>
<script>
    export default {
        name: 'app',
        data: function (){
            return {
                activeIndex:'home',
                menuOpacity:1,
                btnOpacity:0,
                isScroll:false,
                timer:'',
                key:'',
                searchVis:true,
                // ***********************
                relatedBlog:[],
                // ***********************
                tech:[
                    {name:'vue'       , edition:'2.3.3'},
                    {name:'vueRouter' , edition:'2.3.1'},
                    {name:'elementUi' , edition:'1.4.9'},
                    {name:'webpack'   , edition:'2.6.1'},
                    {name:'fa'        , edition:'4.7.0'}
                ]
            }
        },
        methods: {
            // blog  demo 
            toDetails(type,path){
                if( type === 0 ) this.$router.push( '/blog/blog_' + path );
                else if( type === 1 ) this.$router.push( '/demo/demo_' + path );
            },
            // 
            menuClick(){
                const that = this;
                that.returnTop();
                if(location.hash.includes('blog')) return false;
                that.$store.commit('initialSearch');
            },
            // 
            returnTop(){
                document.documentElement.scrollTop = document.body.scrollTop = 0;
            },
            // 
            goRouter(tab){
                this.$router.push(tab);
            },
            // 
            search(){
                const that = this;
                if(that.key.replace(/\s/g) === '') return false;
                that.$store.commit('search',that.formatSearch(this.key));
                that.key = '';
                if(!location.hash.includes('blog')){
                    that.goRouter('/blog')
                }
            },
            // 
            formatSearch(key){
                return key.toLowerCase().replace(/\s/g,'');
            },
            // 
            querySearch(queryString, cb) {
                const that = this;
                const key = that.formatSearch(queryString);
                that.relatedBlog = [];
                that.$store.state.initialBlogData.forEach( (e,i) => {
                    if( that.formatSearch(e.brief).includes(key) || that.formatSearch(e.title).includes(key) ){
                        that.relatedBlog.push({value:e.title,index:e.url});
                    }
                });
                cb(that.relatedBlog);
            },
            // 
            handleSelect(item) {
                const that = this;
                that.$router.push('/blog/blog_'+item.index);
                that.key = '';
            },
            // 
            searchInMobile(){
                const that = this;
                // that.searchVis = !that.searchVis;
                that.goRouter('/search');
            }
        },
    }
</script>
<style>
    /*
        main color #23aa23 -> 
                     #32be32 -> 
                     #f9fafc -> 

    */
    /*a{cursor:pointer;color:#20A0FF;font-size:14px;}*/
    a:link{text-decoration:none;color:#20A0FF;}
    a:visited {text-decoration:none;color:#1D8CE0;}
    a:hover {text-decoration:underline;color:#58B7FF;}
    a:active {text-decoration:none;color:#1D8CE0;}
    .text-center{
        text-align:center;
    }
    .container.overflow-hidden{
        margin:0;
    }
    .header{
        width:100%;
        height:50px;
        background:#23aa23;
        position: fixed;
        top:0;
        z-index:888;
        -webkit-transition: all 0.5s ease-in;
        -moz-transition: all 0.5s ease-in;
        -ms-transition: all 0.5s ease-in;
        -o-transition: all 0.5s ease-in;
        transition: all 0.5s ease-in;
    }
    .header .menu{
        background: #23aa23;
        margin: 0 auto;
    }
    .container{
        position: relative;
    }
    .header .menu .search{
        float: right;
        width: 220px;
        margin-top: 7px;
    }
    .header .menu .item{
        color: #fff;
        height: 50px;
        font-size:16px;
        line-height: 50px;
        border-bottom: 0;
        -webkit-transition: all 0.3s ease-in;
        -moz-transition: all 0.3s ease-in;
        -ms-transition: all 0.3s ease-in;
        -o-transition: all 0.3s ease-in;
        transition: all 0.3s ease-in;
    }
    main.container{
        margin: 0 auto 80px auto;
        padding-top:70px;
        overflow:hidden;
        min-height: 600px;
    }
    main.container section.main-left{
        margin-right:2%;
    }
    main.container section.main-right{
        background:#fff;
        width:34%;
        max-width:300px;
        min-height:600px;
        float:left;
    }
    main.container section.main-left,main.container section.main-right{
        -webkit-box-shadow:0 0 10px 0 #eee;
        -moz-box-shadow: 0 0 10px 0 #eee;
        box-shadow: 0 0 10px 0 #eee;
    }
    #my-blog .mobile-search{
        display:none;
        color:#fff;
    }
    main.container section.main-left{
        background: #fff;
        width:64%;
        min-height:600px;
        float:left;
        overflow:hidden;
    }
    footer.footer{
        width:100%;
        /*position:fixed;*/
        z-index:3;
        bottom:0;
        overflow: hidden;
        min-height:60px;
        background:#47a947;
        color: rgba(154, 237, 144, 0.37);
        text-align:center;
        display: table;
        -webkit-transition: color 0.3s ease-in;
        -moz-transition: color 0.3s ease-in;
        -ms-transition: color 0.3s ease-in;
        -o-transition: color 0.3s ease-in;
        transition: color 0.4s ease-in;
    }
    footer.footer .footer-inner{
        display: table-cell;
        vertical-align:middle;
    }
    footer.footer .tech{
        margin:4px;
    }
    @media screen and ( min-width: 1400px ){
        .header .menu,main.container,.footer-inner{
            width: 1200px;
        }
        main.container section.main-left{
            width:73%;
        }
    }
    @media screen and ( min-width: 1000px ) and ( max-width: 1400px ){
        .header .menu,main.container,.footer-inner{
            width: 800px;
        }
    }
    @media screen and ( min-width: 768px ) {
        .header:hover .search, .header:hover .item {
            display: block;
        }
        footer.footer:hover{
            color:#F9FAFC;
        }
        .header .menu .item:hover{
            color: #fff;
            background: #32be32;
            /*border-bottom:2px solid #fff;*/
        }
        .header:hover{
            opacity:1 !important;
        }
    }
    @media screen and ( max-width: 768px ){
        #my-blog .el-menu--horizontal.el-menu--dark .el-submenu .el-menu-item:hover,
        #my-blog .el-menu--horizontal.el-menu--dark .el-submenu .el-submenu-title:hover,
        #my-blog .el-menu-item:hover{
            background-color:#32be32;
        }
        #my-blog main.container section.main-right,footer.footer .tech .edition{
            display:none;
            background:#23aa23;
        }
        #my-blog main.container .lastest-text{
            margin:10px 0 0 0;
        }
        #my-blog .el-tag{
            height:20px;
            line-height:18px;
        }
        .header .menu .fa-bars{
            display:block;
        }
        main.container{
            margin: 0 auto 10px auto;
            padding-top:60px;
        }
        footer.footer{
            height:36px;
            min-height:36px;
            font-size:12px;
            color:#f9fafc;
        }
        #my-blog main.container section.main-left{
            width:100%;
        }
        #my-blog main.container article{
            height:116px;
        }
        #my-blog main.container article p{
            margin: 4px 0;
        }
        main.container .round-title{
            font-size:16px;
            bottom:20px;
        }
        #my-blog .mobile-search{
            display:inline-block;
            position:absolute;
            top:16px;
            right:24px;
            font-size:18px;
        }
        #my-blog .el-pagination{
            display:none;
        }
    }
    @media screen and ( max-width: 600px ){
        .header .menu,main.container,.footer-inner{
            width: 100%;    
        }
        #my-blog main.container section.main-right{
            display: none;  
        }
        .header .menu .search{
            width:0;
            height:0;
            display: none;
            overflow:hidden;
        }
        .header .menu .search .el-input__icon.el-icon-search.is-clickable{
            display:none;
        }
    }

</style>


