<template>
    <main id="blogList">
        <section class="list-left">
            <my-blog v-for  = "item in $store.state.currentBlogData"
                     :title = "item.title"
                     :url   = "item.url"
                     :key   = "item.url"
                     :time  = "item.time"
                     :tags  = "item.tags"
                     :brief = "item.brief"></my-blog>
                     
            <footer class="text-center">
                <el-pagination
                        @size-change="changeSize"
                        @current-change="changePage"
                        :current-page.sync="currentPage"
                        :page-sizes="[6, 10, 20]"
                        :page-size="$store.state.pageSize"
                        layout="total, prev, pager, next"
                        :total="$store.state.totalBlogLength">
                </el-pagination>
            </footer>
        </section>
        <section class="list-right">
            <div class="chosen">
                <div class="classify">
                    <p class="title"></p>
                    
                </div>
                <div class="latest">
                    <p class="title"></p>
                    10
                    <p class="content">new Date()  2016-9-13 javascript</p>
                </div>
            </div>
        </section>
    </main>
</template>
<script>
    export default {
        data: function (){
            return {
                currentPage: 1
            }
        },
        methods: {
            // 
            changePage(val){
                this.currentPage = val;
                this.getData();
            },
            // 
            changeSize(val){
                this.pageSize = val;
                this.getData();
            },
            // PC 
            getData(){
                //  pageSize  currentPage 
                const that = this;
                const params = {
                    type: 0, // 0 blog , 1 demo
                    currentPage: that.currentPage
                };
                that.$store.commit( 'pageChange', params );          
            },
            //  
            getDataInMobile(){
                const that = this;
                const params = {
                    type: 0,
                    currentPage: that.currentPage
                };
                that.$store.commit( 'loadNextPage', params );
            },
            // 
            scrollToFooter(){

            }
        },
        mounted(){
            if(location.hash ==='#/blog') document.title = ' by  - blogList';
        }
    }
</script>
<style>
    #blogList .list-right .chosen{
        margin:10px;
    }
    #blogList .list-left,#blogList .list-right{
        float:left;
        background: #fff;
        overflow:hidden;
        -webkit-box-shadow: 0 0 10px 0 #eee;
        -moz-box-shadow: 0 0 10px 0 #eee;
        box-shadow: 0 0 10px 0 #eee;
    }
    #blogList .list-left{
        min-height:600px;
        width:64%;
        margin-right:2%;
    }
    #blogList .list-right{
        width:30%;
    }
    #blogList .list-right .chosen .title{
        color:#333;
        font-size:16px;
    }
    #blogList .list-right .chosen .content{
        color:#999;
        font-size:14px;
    }
    #blogList .el-pager li:hover{
        color: #23aa23;
    }
    #blogList .el-pagination__sizes .el-input .el-input__inner:hover{
        border-color: #23aa23;
    }
    #blogList .el-select-dropdown__item:hover{
        background-color: #23aa23;
    }
    #blogList .el-select-dropdown__item.selected.hover{
        background-color: #23aa23;
    }
    #blogList .el-pager li.active{
        border-color: #23aa23;
        background-color: #23aa23;
        color:#fff;
    }
    #blogList footer{
        margin-bottom:10px;
    }
    @media screen and ( max-width: 768px ){
        #blogList .list-left{
            width:100%;
        }
        #blogList .list-right{
            display:none;
        }
    }
</style>


